import Perf from "../json/aesop.json";

export default function Perfume(){

    return(
        
        <div className="sub">
            <h2>Perfume</h2>
            <ul>
            {
                Perf.Perfume.map((p,k)=>(
                    <li key={k} className="subManu">
                        <a>
                        <img src={p.img}/>
                        </a>
                        <p className="subText">
                            <b>{p.tit}</b>
                            <span>{p.text}</span>
                        </p>
                    </li>
                ))
            }
            </ul>
        </div>
    );
}