import Main from "../json/aesop.json";

export default function Perfume(){
    let imgS = [];
    let a = Main.Perfume.forEach((p,k)=>{
                if(k < 4){
                    imgS.push({src:p.img,tit:p.tit})
                }
            });
    //console.log(imgS)

    let imgSr = [];
    let b = Main.BodyHand.forEach((b,k)=>{
                if(k < 6){
                    imgSr.push({src:b.img})
                }
    })
    //console.log(imgSr)
    
    let imgSrc = [];
    let c = Main.Skin.forEach((s,k)=>{ 
                if(k < 4){
                    imgSrc.push({src:s.img,tit:s.tit})
                }
    })
    //console.log(imgSrc)

    return(
        
        <section>

        <div className="main">
          <img src="img/Aesop01.webp"/>
          <div className="maintxt">
            <h2>홈 케어 기프트</h2>
            <p>평온함이나 즐거움, 호기심을 불러일으켜 거실에서 사무실과 같은 개인 공간을 보다 아름답게 가꿔주는 섬세하게 제작된 아로마</p>
          </div>
        </div>

        <div className="mains">
            <ul>
                
                {
                    imgS.map((m,k)=>(
                        <li key={k}>
                            <a>
                                <img src={m.src}/>
                            </a>
                            <span>{m.tit}</span>
                        </li>
                    ))
                }
            </ul>
        </div>

        <div className="mainsu">
            <ul>
                
                {
                   
                   imgSr.map((b,k)=>(
                        <li key={b}>
                            <a>
                            <img src={b.src}/>
                            </a>
                        </li>
                    ))
                }
            </ul>
        </div>

        <div className="mainsub">
            <ul>
                
                {
                    imgSrc.map((s,k)=>(
                        <li key={k}>
                            <a>
                                <img src={s.src}/>
                            </a>
                            <span>{s.tit}</span>
                        </li>
                    ))
                }
            </ul>
        </div>
      
      </section>

    );
}