import Skins from "../json/aesop.json";

export default function Skin(){
    console.log(Skins)
    return(
        <div className="sub">
            <h2>Skin</h2>
            <ul>
            {
                Skins.Skin.map((s,k)=>(
                    <li key={k} className="subManu">
                        <a>
                        <img src={s.img}/>
                        </a>
                        <p className="subText">
                            <b>{s.tit}</b>
                            <span>{s.text}</span>
                        </p>
                    </li>
                ))
            }
            </ul>
        </div>
    );
}
